export default {
	data() {
		return {
			num:1,
			exchangeInfo:{},
			currentIndex:0,
			goodsInfo:{}
		}
	},
	onLoad(e) {
		this.currentIndex=e.index;
		this.getBeforePageInfo();
		this.getGoodInfo();
	},
	methods: {
		//获取商品的信息
		getGoodInfo() {
			let that = this;
			that.baseGet(
				that.U({
					c: 'store_api',
					a: 'details',
					q: {
						id: that.exchangeInfo.product_id
					}
				}),
				function(res) {
					that.goodsInfo = res.data;
					console.log(that.goodsInfo);
				},
				function(res) {
					console.log(res);
				},
				true
			);
		},
		//确认兑换商品
		confirmExchange(){
			let that = this;
			if(that.num>that.exchangeInfo.exchange_num){
				uni.showToast({
					title: '兑换的数量超出~',
					position: 'bottom',
					icon: 'none'
				});
				return ;
			}
			
			if(!that.goodsInfo.storePack){
				uni.showToast({
					title: '兑换商品异常~',
					position: 'bottom',
					icon: 'none'
				});
				return ;
			}

			that.baseGet(
				that.U({
					c: 'auth_api',
					a: 'now_buy',
					q: {
						productId: that.goodsInfo.storeInfo.id,
						merId: that.goodsInfo.merInfo.id,
						cartNum: that.num
					}
				}),
				function(res) {
					uni.navigateTo({
						url: '/pages/subscribe/exchange_confirm/exchange_confirm?listId=' + res.data.cartId
					});
				},
				function(res) {
				},
				true
			);
		},
		//获取上个页面显示的单条数据
		getBeforePageInfo(){
			let pages = getCurrentPages();  //获取所有页面栈实例列表
			let prevPage = pages[ pages.length - 2 ];  //上一页页面实例
			this.exchangeInfo=prevPage.$vm.exchangeInfo[this.currentIndex];
		},
		//商品加减操作
		addSubNum(index,number) {
			if (number == 0 && this.num <= 0) {
				uni.showToast({
					title: '兑换数不能减少了哟~',
					position: 'bottom',
					icon: 'none'
				});
			} else {
				this.num = this.num + number;
			}
		},
	}
}
